* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

.weather-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

.input-container {
  margin-bottom: 20px;
}

input {
  padding: 1em;
  margin-right: 1em;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%; /* Take up full width of the container */
}

button {
  padding: 8px 16px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

button:hover {
  background-color: #45a049;
}

.weather-info {
  background-color: #f9f9f9;
  padding: 16px;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

h1 {
  margin-bottom: 20px;
}

h2,
h3 {
  color: #333;
}

h3 {
  margin-top: 10px;
}

p {
  margin: 8px 0;
  color: #555;
}

.bg-container {
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1);
  background-color: transparent;
  border-radius: 40px;
  padding: 5vw; /* Adjust padding using viewport width units */
}

/* Media queries for responsiveness */
@media (max-width: 600px) {
  input {
    width: 100%; /* Adjust input width for smaller screens */
  }

  .bg-container {
    padding: 2em; /* Adjust padding for smaller screens */
  }
}
